
div.SearchContainer {
    align-items: center;
    display: flex;
    flex-direction: column;
    width: 95vw;
    max-width: 600px;
    margin: 0 auto;
    
}


/*  container styling */
div.SearchContainer div.FormContainer {
    border-style: solid;
    border-color: #8E7A3D;
    border-width: 1px;
    padding: 20px;
    background-color: #D7B957;
    border-radius: 5px;
    margin: 10rem auto 10px auto;
    box-shadow: 1px 1px 8px 1px rgba(55, 50, 62, .5);
    animation: FadeIn 1s;
}

div.SearchContainer div.SearchOptionsContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

div.SearchContainer div.SubmitButtonContainer {
    display: flex;
    justify-content: center;
}

div.SearchContainer section.SearchOptionSection {
    display: flex;
    flex-direction: column;
}

div.SearchContainer div.checkboxContainer {
    margin: 5px 0;
    display: flex;

}

div.SearchContainer h3.fontTitles {
    color: #582E44;
    text-align: center;
    margin: 0 0 5px 0;
}

div.SearchContainer label.fontLabels {
    font-family: 'Raleway', sans-serif;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: flex-start;
}

div.SearchContainer section.healthOptions {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 5px;
}

div.SearchContainer label.optionsTitles {
    font-family: 'Crete Round', serif;
    background-color: #582E44 ;
    color: #D7B957;
    padding: .25rem;
    border-radius: .25rem;
    text-align: center;
    margin-bottom: 0;
}

div.SearchContainer div.searchButtonDiv {
    display: flex;
    flex-direction: column;
}

div.SearchContainer label.searchLabel {
    text-align: center;
    margin: 8px 0 3px 0;
    padding: 10px;
    border-top: solid;
    border-top-color: rgba(88, 46, 68, 0.8);
}

div.SearchContainer p.optionsTitles {
    font-family: 'Crete Round', serif;
    background-color: #582E44 ;
    color: #D7B957;
    padding: .25rem;
    margin: 0 .25rem 0 .25rem;
    border-radius: 4px;
    text-align: center;
    width: 150px;
}

div.SearchContainer input.searchInput {
    height: 30px;
    border-radius: 10px;
}

.SubmitButton {
    background: #582E44;
    color: #FFFBDB;
    margin: 10px auto 0 auto;
    width: 100%;
    height: 40px;
    border: none;
    border-radius: 15px;
    padding: 5px;
    box-shadow: .5px .5px 4px 1px rgba(55, 50, 62, .5);
    transition: all 0.3s;
}

.SubmitButton:hover {
  opacity: .9;
}

input {
    background: #FFFBDB;
    border: none;
    border-radius: 15px;
}

div.SearchContainer div.calorie-and-count {
    display: flex;
    flex-direction: row;
}

div.SearchContainer p.calorieCountTrackerP {
    margin: 0;
    background-color: none;
}

div.SearchContainer label.calorieTitleLabel {
    width: 100%;
    margin: 0 .25rem 0 .25rem;
}


/* range styling */



input[type=range] {
    height: 25px;
    -webkit-appearance: none;
    margin: 10px 0;
    width: 100%;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 8px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000;
    background: #582E44;
    border-radius: 4px;
    border: 0px solid #14A3A3;
  }
  input[type=range]::-webkit-slider-thumb {
    box-shadow: 0px 0px 0px #000000;
    border: 1px solid #000000;
    height: 18px;
    width: 15px;
    border-radius: 4px;
    background: rgb(233, 214, 153);
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -5.5px;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: #582E44;
  }
  input[type=range]::-moz-range-track {
    width: 100%;
    height: 8px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000;
    background: #582E44;
    border-radius: 4px;
    border: 0px solid #14A3A3;
  }
  input[type=range]::-moz-range-thumb {
    box-shadow: 0px 0px 0px #000000;
    border: 1px solid #000000;
    height: 18px;
    width: 15px;
    border-radius: 4px;
    background: #FFFFFF;
    cursor: pointer;
  }
  input[type=range]::-ms-track {
    width: 100%;
    height: 8px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
  }
  input[type=range]::-ms-fill-lower {
    background: #582E44;
    border: 0px solid #14A3A3;
    border-radius: 8px;
    box-shadow: 0px 0px 0px #000000;
  }
  input[type=range]::-ms-fill-upper {
    background: #582E44;
    border: 0px solid #14A3A3;
    border-radius: 8px;
    box-shadow: 0px 0px 0px #000000;
  }
  input[type=range]::-ms-thumb {
    margin-top: 1px;
    box-shadow: 0px 0px 0px #000000;
    border: 1px solid #000000;
    height: 18px;
    width: 15px;
    border-radius: 4px;
    background: #582E44;
    cursor: pointer;
  }
  input[type=range]:focus::-ms-fill-lower {
    background: #582E44;
  }
  input[type=range]:focus::-ms-fill-upper {
    background: #582E44;
  }


/*  Desktop version */

  @media only screen and (min-width: 600px) {

    div.SearchContainer div.SearchOptionsContainer {
        display: flex;
        flex-direction: row;
    }

    div.SearchContainer section.healthOptions {
        margin: 0 5px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    div.SearchContainer div.calorie-and-count {
        margin: 0px 0 0 0;
    }
    div.SearchContainer label.calorieTitleLabel {
        width: 150px;
        margin:  0 0 5px 0;
    }
    div.SearchContainer form.mainForm {
        width: 500px;
    }
    div.SearchContainer label.optionsTitles {
        font-family: 'Crete Round', serif;
        background-color: #582E44 ;
        color: #D7B957;
        padding: .25rem;
        border-radius: 4px;
        text-align: center;
        width: 150px;
    }
  }

  input[type="checkbox"] {
    /* visibility: hidden; */
    display: none;
  }
  label {
    cursor: pointer;
  }
  input[type="checkbox"] + label:before {
    border: 1px solid #333;
    content: "\00a0";
    display: inline-block;
    font: 16px/1em sans-serif;
    height: 16px;
    margin: 0 .25em 0 .25em;
    padding: 0;
    vertical-align: top;
    width: 16px;
    background: #FFFBDB;
    border-radius: 2.5px;

  }
  input[type="checkbox"]:checked + label:before {
    color: #333;
    content: "\2713";
    text-align: center;
  }
  input[type="checkbox"]:checked + label:after {
    font-weight: bold;
    color: #582E44;
  }
  
  input[type="checkbox"]:focus + label::before {
      outline: rgb(59, 153, 252) auto 5px;
  }

  .calorieContainer {
    display: flex;
    width: 100%;
    justify-content: center;
  }



  